/* CSS Document */

header {
  z-index: 9;
  width: 100%;
  min-width: 4em;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.toggle,
[id^="drop"] {
  display: none;
}

/* Giving a background-color to the nav container. */

nav {
  margin: 0;
  padding: 0;
}

a.navbar-brand,
.footer h2 a {
  color: inherit;
  font-size: 33px;
  font-weight: 400;
  letter-spacing: 0px;
}

#logo a {
  float: left;
  display: inline;
  display: initial;
}

/* Since we'll have the "ul li" "float:left"
 * we need to add a clear after the container. */

nav:after {
  content: "";
  display: table;
  clear: both;
}

/* Removing padding, margin and "list-style" from the "ul",
 * and adding "position:reltive" */

nav ul {
  float: right;
  padding: 0;
  margin: 0;
  list-style: none;
  position: relative;
}

/* Positioning the navigation items inline */

nav ul li {
  margin: 0px;
  display: inline-block;
}

/* Styling the links */

nav a {
  color: inherit;
  font-size: 15px;
  letter-spacing: 1px;
  padding: 10px 0;
  font-size: 15px;
  letter-spacing: 1px;
  padding: 0 14px;
}

nav ul li ul li:hover {
  background: #f8f9fa;
}

/* Background color change on Hover */

nav li a:hover {
  color: #ef9632;
}

.menu li.active a {
  color: #ef9632;
}

/* Hide Dropdowns by Default
 * and giving it a position of absolute */

li.dropdown {
  position: relative;
}

nav ul ul {
  display: none;
  position: absolute;
  /* has to be the same number as the "line-height" of "nav a" */
  top: 25px;
  left: 0;
  padding: 10px;
  background: #fff;
  padding: 10px;
  z-index: 999;
  border: 1px solid #ddd;
}

/* Display Dropdowns on Hover */

nav ul li:hover > ul {
  display: inherit;
}

/* Fisrt Tier Dropdown */

nav ul ul li {
  width: 170px;
  float: none;
  display: list-item;
  position: relative;
}

nav ul ul li a {
  color: #333;
  padding: 5px 10px;
  display: block;
}

/* Second, Third and more Tiers	
 * We move the 2nd and 3rd etc tier dropdowns to the left
 * by the amount of the width of the first tier.
*/

nav ul ul ul li {
  position: relative;
  top: -60px;
  /* has to be the same number as the "width" of "nav ul ul li" */
  left: 170px;
}

/* Change ' +' in order to change the Dropdown symbol */

li > a:only-child:after {
  content: "";
}

/* Media Queries
--------------------------------------------- */

@media all and (max-width: 768px) {
  #logo {
    display: block;
    padding: 0;
    width: 100%;
    text-align: center;
    float: none;
  }

  nav {
    margin: 0;
  }

  /* Hide the navigation menu by default */
  /* Also hide the  */
  .toggle + a,
  .menu {
    display: none;
  }

  /* Stylinf the toggle lable */
  .toggle {
    display: block;
    /* padding: 7px 20px; */
    font-size: 30px;
    text-decoration: none;
    border: none;
    float: right;
    /* background-color: #212529; */
    color: #fff;
    cursor: pointer !important;
    margin-bottom: 0;
    text-transform: uppercase;
    /* margin-top: 0.7em; */
  }
  .toggle i {
    color: #000;
  }

  .menu .toggle {
    float: none;
    text-align: center;
    margin: auto;
    width: 80%;
    padding: 5px;
    font-weight: normal;
    font-size: 16px;
    letter-spacing: 1px;
  }

  .toggle:hover {
    color: #333;
  }

  /* Display Dropdown when clicked on Parent Lable */
  [id^="drop"]:checked + ul {
    display: block;
    background: rgba(16, 16, 16, 0.85);
    padding: 15px 0;
    text-align: center;
    width: 100%;
  }

  /* Change menu item's width to 100% */
  nav ul li {
    display: block;
    width: 100%;
    padding: 5px 0;
  }

  nav ul ul .toggle,
  nav ul ul a {
    padding: 0 40px;
  }

  nav ul ul ul a {
    padding: 0 80px;
  }

  nav a:hover,
  nav ul ul ul a {
    background-color: transparent;
    color: #fff;
  }

  nav ul li ul li .toggle,
  nav ul ul a,
  nav ul ul ul a {
    padding: 14px 20px;
    color: #fff;
    font-size: 17px;
  }

  nav ul li ul li .toggle,
  nav ul ul a {
    background-color: #212121;
  }

  /* Hide Dropdowns by Default */
  nav ul ul {
    float: none;
    position: static;
    color: #ffffff;
    /* has to be the same number as the "line-height" of "nav a" */
  }

  /* Hide menus on hover */
  nav ul ul li:hover > ul,
  nav ul li:hover > ul {
    display: none;
  }

  /* Fisrt Tier Dropdown */
  nav ul ul li {
    display: block;
    width: 100%;
    padding: 0;
  }

  nav ul ul ul li {
    position: static;
    /* has to be the same number as the "width" of "nav ul ul li" */
  }

  nav ul ul li a {
    color: #fff;
    font-size: 0.85em;
  }

  nav ul li a {
    color: #fff;
  }
}

@media all and (max-width: 330px) {
  nav ul li {
    display: block;
    width: 94%;
  }
}

